import React from "react";
import { Switch, Route } from "react-router-dom";

import { Row, Col } from "react-bootstrap";
import Menu from "./components/menu/menu.component";
import Intro from "./components/intro/intro.component";
import Slideshow from "./components/slideshow/slideshow.component";
import { ticTacToe } from "./project/tic-tac-toe/tic-tac-toe";
import { eCommerce } from "./project/e-commerce/e-commerce";

import "./App.css";

function App() {
  return (
    <div className="App">
      <Row>
        <Menu />
        <Col className="main">
          <Switch>
            <Route exact path="/" component={Intro} />
            <Route path="/tictactoe" render={() => <Slideshow slides={ticTacToe} />} />
            <Route path="/ecommerce" render={() => <Slideshow slides={eCommerce} />} />
          </Switch>
        </Col>
      </Row>
    </div>
  );
}

export default App;
